.modal {
  //   display: block;
  //   position: fixed;
  z-index: 1;
  background-color: rgba(130, 71, 71, 0.448);
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 40px 12px;
  border: 1px solid #888;
  width: 60%;
  & img {
    width: 375px;
    height: 478px;
  }
}

#movies-library {
  height: 100vh;
}

.radio-home {
  visibility: hidden;
}
